.Node {
  background-color: var(--leaf-background);
  width: 75px;
  height: 75px;
  opacity: 0.88;
  transition: background 1s;
}

.Node .Node {
  transform: scale(0.707) translateY(-140%) translateX(-70%) rotate(-47deg);
}

.Node + .Node {
  transform: scale(0.707) translateY(-280%) translateX(70%) rotate(47deg);
}

.Node.base {
  margin-bottom: 10vh;
}

.Node--even-level {
}

.Node--full {
  background: var(--leaf-full);
}
